<template>
<el-card>
    <div slot="header">
        <span>游戏行为查询</span>
    </div>
    <!-- 搜索 -->
    <div>
        <el-form :model="objSearch" label-width="100px">
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="角色ID">
                        <el-input v-model="objSearch.pid" placeholder="请输入角色ID"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="服务器ID">
                        <el-input v-model="objSearch.serverId" placeholder="请输入服务器ID"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="参数">
                        <el-input v-model="objSearch.param" placeholder="请输入参数"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="handleSearch">搜索</el-button>
                    <el-button type="primary" @click="handleReset">重置</el-button>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-form-item label="开始">
                        <el-date-picker v-model="objSearch.start" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择开始时间"></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="结束">
                        <el-date-picker v-model="objSearch.end" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择结束时间"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col>
                    <el-form-item label="玩家行为">
                        <el-checkbox-group v-model="objSearch.action">
                            <el-checkbox v-for="(item,idx) in aActions" :label="item.action" :key="idx">{{item.name}}</el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
    <!-- 表格 -->
    <div>
        <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%">
            <template v-for="(item,idx) in aTableColumn">
                <el-table-column :key="idx" :prop="item.field" :label="item.tableHead" min-width="120" show-overflow-tooltip></el-table-column>
            </template>
        </el-table>
    </div>
</el-card>
</template>

<script>
export default {
    name: "behavior",
    components: {},
    props: {},
    data() {
        return {
            objSearch: {
                action: []
            },
            aTableColumn: [],
            aTableData: [],
            aActions: []
        };
    },
    watch: {},
    methods: {
        // 重置
        handleReset() {
            this.objSearch = {
                action: []
            };
        },
        //   搜索
        handleSearch() {
            this.fetchList();
        },
        // 列表
        fetchList() {
            let params = JSON.parse(JSON.stringify(this.objSearch));
            this.$fetch
                .post(API.action.select_player_action, params)
                .then(response => {
                    let res = response.data;
                    if (res.status == 200) {
                        let data = res.data;
                        this.aTableData = data.fieldList;
                        this.aTableColumn = data.headList;
                    } else {
                        this.$message.error(res.desc);
                    }
                });
        },

        fetchGames() {
            this.$fetch.post(API.action.action_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aActions = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchGames();
    }
};
</script>

<style lang="stylus" scoped></style>
